.carousel {
    position: relative;
    width: 100%;
    max-width: 100%;
    margin-top: 118px;
  }
  
  .carousel-inner {
    display: flex;
    transition: transform 0.5s ease-in-out;
    position: relative;
    width: 100%;
    height: 660px;
    background-size: cover;
    background-position: center;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }

  @media (max-width: 600px) {
  .carousel-inner {
    height: 755px;
    }
  }

  .carousel-content {
    position: absolute;
    width: 550px;
    bottom: 170px;
    left: 120px;
    color: #fff;
    text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.5);
  }

  h1 {
    font-size: 70px;
    line-height: 1.2em; /* Interlineado de 1.5 veces el tamaño de la fuente */
  }

  p{
    font-size: 35px;
    margin-top: 20px; /* Reducir el margen superior del p */
    line-height: 1.2em; /* Interlineado de 1.5 veces el tamaño de la fuente */
  }
  
  .prev-btn,
  .next-btn {
    position: absolute;
    top: 47%;
    transform: translateY(-50%);
    background-color: rgba(0, 0, 0, 0.5);
    color: #fff;
    font-size: 24px;
    padding: 10px;
    border: none;
    cursor: pointer;
  }
  
  .prev-btn {
    left: 20px;
  }
  
  .next-btn {
    right: 20px;
  }
  
  .carousel-content .custom-button {
    background-color: #F54B4B;
    color: #054b16;
    padding: 10px 20px;
    top: 40px;
    border: none;
    cursor: pointer;
    position: relative; /* Permite posicionar el span dentro del botón */
    font-weight: bold; /* Hace la letra más gruesa */
  }
  
  .carousel-content .custom-button span#bar {
    position: absolute;
    bottom: 8px;
    left: 18px;
    width: 75%;
    height: 1.5px;
    background-color: #054b16;
    display: none; /* Ocultar la barra por defecto */
  }
  
  .carousel-content .custom-button:hover span#bar {
    display: block; /* Mostrar la barra al pasar el mouse por encima del botón */
  }

  .carousel-content .custom-button:hover {
    background-color: red; /* Cambiar color al pasar el mouse por encima */
  }