.container {
  display: grid;
  grid-template-columns: repeat(2, 1fr); /* Dos columnas de igual ancho */
  gap: 5px; /* Espacio entre las cajas */
  margin-top: 20px; /* Espacio entre las filas (verticalmente) */
  margin: 10px;
}

.container2 {
  display: grid;
  grid-template-columns: repeat(1, 1fr); /* Dos columnas de igual ancho */
  gap: 5px; /* Espacio entre las cajas */
  margin: 10px;
}

.customBox {
  position: relative;
  border: 1px solid #000000;
  height: auto; /* Altura automática para ajustar contenido */
  margin-bottom: 5px; /* Espacio inferior para separar las cajas */
  overflow: hidden;
}

.customBox4 {
  position: relative;
  border: 1px solid #000000;
  height: 650px !important; /* Altura automática para ajustar contenido */
  margin-bottom: 5px; /* Espacio inferior para separar las cajas */
  overflow: hidden;
}

.customBox5{
  position: relative;
  border: 1px solid #000000;
  height: 650px !important; /* Altura automática para ajustar contenido */
  margin-bottom: 5px; /* Espacio inferior para separar las cajas */
  margin-top: -11px;
  overflow: hidden;
}

.box1 {
  background-color: #FF0000;
  color: #e7e7e7;
  height: 850px; /* Altura de la primera caja en la primera columna */
}

.box2 {
  background-color: #ff0000;
  color: #fff;
  height: 425px; /* Altura de la segunda caja en la primera columna */
}

.box3 {
  background-color: red  ;
  color: #08c5cf;
  margin-bottom: 5px;
  height: 425px; /* Altura de la primera caja en la segunda columna */
  transition: margin-bottom 0.3s; /* Aplica transición en el margen inferior */
}

.box4 {
  background-color: #FF0000;
  color: #333;
  height: 850px; /* Altura de la segunda caja en la segunda columna */
  transition: margin-bottom 0.3s; /* Aplica transición en el margen inferior */
}

.box5 {
  width: 100%;
  background-color: #FF0000;
  color: #333;
  height: 800px; /* Altura de la segunda caja en la segunda columna */
}

/* Media query para dispositivos móviles */
@media (max-width: 600px) {
  .container {
    grid-template-columns: 1fr; /* Cambia a una sola columna en dispositivos móviles */
  }
  .customBox {
    max-height: 650px; /* Elimina la altura máxima en dispositivos móviles */
  }
  .textBox {
    bottom: 20px !important;
    left: 0 !important;
    right: 10px;
    padding: 10px;
    font-size: 12px;
  }

  .textBox h1 {
    font-size: 30px;
  }

  .textBox h5 {
    font-size: 16px;
  }

  .customBox1 {
    display: block !important; /* Cambia a un bloque para una sola columna en dispositivos móviles */
  }
  

  .imgContainer {
    text-align: center; /* Centra la imagen horizontalmente */
  }

  .imgContainer img {
    width: 100%; /* Ajusta el ancho de la imagen al máximo */
    height: auto; /* Ajusta la altura de la imagen de forma automática */
    display: block; /* Ajusta la visualización de la imagen como bloque */
    margin-bottom: 20px; /* Añade espacio inferior entre la imagen y el texto */
  }
}

@media (max-width: 600px) {
  .customBox1 {
    display: block !important;
    height: 530px; /* Establece la altura máxima de la caja en dispositivos móviles */
    overflow: hidden; /* Oculta el contenido que desborda de la caja */
  }

  .textBox3 {
    padding: 20px; /* Ajusta el espacio interno del texto */
    text-align: center; /* Centra el texto horizontalmente */
    margin-top: -20px !important  ;
  }

  .imgContainer {
    text-align: center; /* Centra la imagen horizontalmente */
  }

  .imgContainer img {
    width: 100%; /* Ajusta el ancho de la imagen al máximo */
    height: auto; /* Ajusta la altura de la imagen de forma automática */
    display: block; /* Ajusta la visualización de la imagen como bloque */
    margin-bottom: 20px; /* Añade espacio inferior entre la imagen y el texto */
  }

  .textBox3 {
    padding: 10px;
    font-size: 12px;
    text-align: left;
  }

  .textBox3 h2 {
    font-size: 30px;
  }

  .textBox3 h5 {
    font-size: 16px;
  }
}

.textBox {
  position: absolute;
  bottom: 70px;
  left: 30px;
  right: 0;
  padding: 20px;
  color: #fff; /* Color de texto sólido */
}

.textBox h1,
.textBox h5,
.textBox p {
  margin: 3px;
}

.textBox h1 {
  font-size: 50px;
}

.textBox p {
  font-size: 23px;
  margin-top: 15px; /* Espacio superior al botón */
  margin-bottom: -5px; /* Espacio superior al botón */
}

.ctaText {
  position: relative;
  cursor: pointer;
  transition: color 0.3s; /* Animación de transición para el cambio de color */
}

.ctaText:hover {
  color: #ffcc80; /* Cambia el color del texto al pasar el mouse sobre el texto */
}

.ctaText::after {
  content: '';
  position: absolute;
  bottom: -3px; /* Ajusta la posición de la barra debajo del texto */
  left: 0;
  width: 100%;
  height: 2px; /* Altura de la barra */
  background-color: #ffcc80; /* Color de la barra */
  transform: scaleX(0); /* Inicialmente oculta la barra */
  transition: transform 0.3s; /* Animación de transición para mostrar la barra */
}

.ctaText:hover::after {
  transform: scaleX(1); /* Muestra la barra al pasar el mouse sobre el texto */
}

/* Estilos para la segunda caja */
.textBox3 {
  padding: 35px;
  color: #fff; /* Color de texto sólido */
  margin-top: 40px;
}

.textBox3 h1,
.textBox3 h2,
.textBox3 p {
  margin: 1px;
}

.textBox3 p {
  font-size: 21px;
}

.ctaText3 {
  position: relative;
  cursor: pointer;
  transition: color 0.3s; /* Animación de transición para el cambio de color */
}

.ctaText3:hover {
  color: #ffffff; /* Cambia el color del texto al pasar el mouse sobre el texto */
}

.ctaText3::after {
  content: '';
  position: absolute;
  bottom: -3px; /* Ajusta la posición de la barra debajo del texto */
  left: 0;
  width: 100%;
  height: 2px; /* Altura de la barra */
  background-color: #ffffff; /* Color de la barra */
  transform: scaleX(0); /* Inicialmente oculta la barra */
  transition: transform 0.3s; /* Animación de transición para mostrar la barra */
}

.ctaText3:hover::after {
  transform: scaleX(1); /* Muestra la barra al pasar el mouse sobre el texto */
}

.imgContainer {
  overflow: hidden; /* Oculta el contenido que se desborda */
}

.imgContainer img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.3s ease; /* Transición suave de 0.3 segundos en la propiedad transform */
}

.imgContainer img:hover {
  transform: scale(1.1); /* Escala la imagen al 110% cuando se pasa el mouse sobre ella */
}

/* BARRA DE ICONOS */
.link-with-underline {
  position: relative;
}

.link-with-underline .underline {
  position: absolute;
  left: 0;
  bottom: 15px;
  width: 100%;
  height: 2px;
  background-color: red;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.link-with-underline:hover .underline {
  opacity: 1;
}
